@media screen and (max-width: 900px) {
  .main-body {
    margin-bottom: 2em;
    background-color: #f6f6f6;
    display: grid;
    grid-template-columns: 5px auto 5px;
    row-gap: 10px;
    > div {      
      grid-column: 2;
      background-color: white;
      border-radius: 3px;
      -moz-border-radius: 3px;
      -webkit-border-radius: 3px;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    }
    #carousel-content {      
      width: 100%;
      max-width: 1170px;
      height: 200px;
      .carousel-indicators {
        bottom: 16px;
        margin: 0;
        height: 0;
        background-color: white;
        > li {
          background-color: red;
        }
      }
      .carousel-inner {
        width: 100%;
        height: 95%;
        .carousel-item {
          width: 100%;
          height: 100%;
          > div:first-child {
            .cover-img-background;
            background-color: red;
          }
          .carousel-caption {
          }
        }
      }
    }
    .event-covid {
      .cover-img-background;
      height: 60px;
    }
    .icon-menu{      
      display:flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      a{
        padding: 10px;
        .flex-center;
        svg{
          width: 30px;
          height: 30px;
          path{
            fill:#444;
          }
        }
        .text-menu{
          font: @font;
          color: #444;
        }     
      }      
    }
  }
}
.cover-img-background {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 100%;
}
.flex-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@font: 14px/18px Helvetica, Arial, "DejaVu Sans", "Liberation Sans", Freesans,
  sans-serif;
body {
  font: @font;
}
